import { Component, Input, OnInit } from '@angular/core';
import { SwiperOptions } from 'swiper';

@Component({
  selector: 'app-e-carousel',
  templateUrl: './e-carousel.component.html',
  styleUrls: ['./e-carousel.component.scss']
})
export class ECarouselComponent implements OnInit {
  @Input() carouselData: any
  swiperConfig: SwiperOptions = {
    breakpoints: {
      0: {
        slidesPerView: 1,
      },
      500: {
        slidesPerView: 2,
      },
      700: {
        slidesPerView: 3,
      },
      1024: {
        slidesPerView: 4,
      },
    },
    loop: true,
    navigation: true,
    autoplay: true,
    spaceBetween: 10,

  }

  constructor() { }

  ngOnInit(): void {
  }

}
