import { HttpResponse } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { DragScrollComponent } from 'ngx-drag-scroll';
import { FlashAnnonce } from 'src/app/core/entity/flash-annonce/flash-annonce';
import { FlashAnnonceService } from 'src/app/core/service/annonce/flash-annonce.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-flash-annonce',
  templateUrl: './flash-annonce.component.html',
  styleUrls: ['./flash-annonce.component.scss']
})
export class FlashAnnonceComponent implements OnInit {

  constructor(
    private flashAnnonceService: FlashAnnonceService
  ) { 
    this.imageUri = environment.api_host + environment.image_url;
  }

  ngOnInit(): void {
    this.flashAnnonceService.GetFlashAnnonce(true, 10).subscribe((response: HttpResponse<FlashAnnonce[]>)=>{
        if(response.body['data']){
          this.flashannonces = response.body['data'];
        }
    });
  }
  flashannonces: Array<FlashAnnonce> = [];
  imageUri: string;
  @ViewChild('nav', {read: DragScrollComponent}) ds: DragScrollComponent;
  
  moveLeft() {
    this.ds.moveLeft();
  }

  moveRight() {
    this.ds.moveRight();
  }

  moveTo(index) {
    this.ds.moveTo(index);
  }

  ngAfterViewInit() {
    // Starting ngx-drag-scroll from specified index(3)
    setTimeout(() => {
      this.ds.moveTo(3);
    }, 0);
  }

}
