import { HttpResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { LoginReq } from 'src/app/core/entity/user/login-req';
import { NewUserReq } from 'src/app/core/entity/user/new-user-req';
import { UserLoginReq } from 'src/app/core/entity/user/user-login-req';
import { UserRegistrationReq } from 'src/app/core/entity/user/user-registration-req';
import { LanguageEnum } from 'src/app/core/enumaration/language-enum';
import { FormDataPipe } from 'src/app/core/pipes/form-data.pipe';
import { AuthService } from 'src/app/core/service/authentication/auth.service';
import { LanguageService } from 'src/app/core/service/language.service';
import { LoadingService } from 'src/app/core/service/loading.service';
import { UserService } from 'src/app/core/service/user/user.service';
import { environment } from 'src/environments/environment';
import { LANGUAGE_EN } from '../../theme/languages/en';
import { LANGUAGE_FR } from '../../theme/languages/fr';
import { LANGUAGE_MG } from '../../theme/languages/mg';
import { DatepickerOptions } from 'ng2-datepicker';
import { getYear } from 'date-fns';
import locale from 'date-fns/locale/en-US';

@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.scss']
})
export class RegistrationComponent implements OnInit {
  step: number = 0;
  passwordValidation: string = "";
  showValidationButton: boolean = false;
  isPasswordValid: boolean = false;
  userLoginReq: UserLoginReq = new UserLoginReq();
  f = {}
  //userRegistrationReq: UserRegistrationReq = new UserRegistrationReq();
  language: any = {};
  constructor(
    private authService: AuthService,
    private toastrService: ToastrService,
    private router: Router,
    private loadingService: LoadingService,
    private userService: UserService,
    private languageService: LanguageService,
    private formBuilder: FormBuilder,
  ) {
    this.newUserReq.birthDate = new Date();
    if (this.languageService.GetLanguage() == LanguageEnum.FR) {
      this.language = LANGUAGE_FR;
    }
    if (this.languageService.GetLanguage() == LanguageEnum.EN) {
      this.language = LANGUAGE_EN;
    }
  }

  formDataPipe = new FormDataPipe();
  newUserReq: NewUserReq = new NewUserReq();
  loginReq: LoginReq = new LoginReq();
  ngOnInit(): void {
    this.loadingService.emitChange(false);
    this.uploadForm = this.formBuilder.group({
      File_1: [''],
    });
  }
  image_1_label: string = "Selection votre iimage profile";
  save() {
    let formData = new FormData();
    formData.append('File_1', this.uploadForm.get('File_1').value);
    formData.append('JsonBody', JSON.stringify(this.newUserReq) as any);

    this.userService.CreateUser(true, formData).subscribe((response: HttpResponse<boolean>) => {
      if (response.status == 200) {
        this.loginReq.password = this.newUserReq.password;
        this.loginReq.username = this.newUserReq.username;

        this.authService.Login(this.loginReq).subscribe((response: HttpResponse<string>) => {
          localStorage.setItem('token', response.body['data']);
          this.router.navigateByUrl(environment.uri_home);
        }, error => {
          this.loadingService.emitChange(false);
        });
      }
    });
  }
  uploadForm: FormGroup;
  onFile1Selected(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.image_1_label = file.name;
      this.uploadForm.get('File_1').setValue(file);
    } else {
      alert("vous dever selectionner un fichier");
    }
  }
  options: DatepickerOptions = {
  minYear: getYear(new Date()) - 30, // minimum available and selectable year
  maxYear: getYear(new Date()) + 30, // maximum available and selectable year
  placeholder: '', // placeholder in case date model is null | undefined, example: 'Please pick a date'
  format: 'LLLL do yyyy', // date format to display in input
  formatTitle: 'LLLL yyyy',
  formatDays: 'EEEEE',
  firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
  locale: locale, // date-fns locale
  position: 'bottom',
  inputClass: '', // custom input CSS class to be applied
  calendarClass: 'datepicker-default', // custom datepicker calendar CSS class to be applied
  scrollBarColor: '#dfe3e9', // in case you customize you theme, here you define scroll bar color
};
  // onCheckPassword() {
  //   if (this.userRegistrationReq.password == this.passwordValidation && this.passwordValidation && this.userRegistrationReq.password) {
  //     this.isPasswordValid = true;
  //   } else {
  //     this.isPasswordValid = false;
  //   }
  // }

  imageURL: string ="https://images.pexels.com/photos/2589653/pexels-photo-2589653.jpeg?auto=compress&cs=tinysrgb&h=650&w=940";
  showPreview(event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.uploadForm.patchValue({
      File_1: file
    });
    this.uploadForm.get('File_1').updateValueAndValidity()

    // File Preview
    const reader = new FileReader();
    reader.onload = () => {
      this.imageURL = reader.result as string;
    }
    reader.readAsDataURL(file)
  }



}
