import { ChangeDetectorRef, Component } from '@angular/core';
import { Router } from '@angular/router';
import { Console } from 'console';
import { CookiesService } from './core/service/cookies.service';
import { LoadingService } from './core/service/loading.service';
import { SweetAlertService } from './core/service/sweet-alert.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  loading: boolean = true;
  title = 'econe-mg';
  constructor(
    private loadingService: LoadingService,
    private cdr: ChangeDetectorRef,
    private cookiesService: CookiesService,
    private sweetAlerService: SweetAlertService,
    private router: Router
  ) {
    this.loadingService.changeEmitted$.subscribe(state => {
      this.loading = state;
      this.cdr.detectChanges();
    });
    if(!this.cookiesService.CheckCondition()){
      this.sweetAlerService.showCookiesAcceptance();
    }

    if(!this.cookiesService.CheckUseTerms()){
      this.sweetAlerService.showUseTerms();
    }
  }
}


