
import { Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { LanguageEnum } from "src/app/core/enumaration/language-enum";
import { CookiesService } from "src/app/core/service/cookies.service";
import { LanguageService } from "src/app/core/service/language.service";
import { SweetAlertService } from "src/app/core/service/sweet-alert.service";
import { environment } from "src/environments/environment";
import Swal from "sweetalert2";
import { HTML_EMOJI } from "../theme/items/emojis/html-emoji";
import { LANGUAGE_EN } from "../theme/languages/en";
import { LANGUAGE_FR } from "../theme/languages/fr";

@Injectable()
export class ApiSweetAlertService extends SweetAlertService {
    showModal(message): void {
        Swal.fire({
            html: message,
            showDenyButton: false,
            showCancelButton: false,
            confirmButtonText: `FERMER`,
            backdrop: false,
            confirmButtonColor: '#F59E0B',
        }).then((result) => {
            // if (result.isConfirmed) {
            //     this.cookiesService.AcceptUserTerms(true);
            // }
        });
    }
    showUseTerms(): void {
        Swal.fire({
            html: HTML_EMOJI.SMILING_FACE_WITH_SUNGLASSES + ' ' + this.userTermsMessage + ' <a class="text-yellow-800 cursor-pointer" href="' + window.location.origin + environment.uri_user_terms + '" target="_blank">' + this.here_link + '</a>',
            showDenyButton: false,
            showCancelButton: false,
            confirmButtonText: `${this.buttonText}`,
            backdrop: false,
            confirmButtonColor: '#F59E0B',
        }).then((result) => {
            if (result.isConfirmed) {
                this.cookiesService.AcceptUserTerms(true);
            }
        });
    }
    showPayment(): void {
        Swal.fire({
            html: "TEST",
            showDenyButton: true,
            showCancelButton: false,
            confirmButtonText: `${this.buttonText}`,
            backdrop: false,
            confirmButtonColor: '#F59E0B',

        }).then((result) => {
            if (result.isConfirmed) {
                this.cookiesService.AcceptCondition(true);
            }
        });
    }
    constructor(
        private cookiesService: CookiesService,
        private languageService: LanguageService,
        private router: Router
    ) {
        super();
        if (this.languageService.GetLanguage() == LanguageEnum.FR) {
            this.cookiesMessage = LANGUAGE_FR.cookies_acceptance;
            this.buttonText = LANGUAGE_FR.yes;
            this.userTermsMessage = LANGUAGE_FR.user_terms;
            this.here_link = LANGUAGE_FR.here;
        }
        if (this.languageService.GetLanguage() == LanguageEnum.EN) {
            this.cookiesMessage = LANGUAGE_EN.cookies_acceptance;
            this.buttonText = LANGUAGE_EN.yes;
            this.userTermsMessage = LANGUAGE_EN.user_terms;
            this.here_link = LANGUAGE_EN.here;

        }
    }
    showToastr(content: string): void {
        Swal.fire({
            html: content,
            toast: false,
            timer: 2000,
            position: "bottom-end",
            showConfirmButton: false,
            width: 250,
            timerProgressBar: true,
        });
    }
    accepted: boolean = false;
    cookiesMessage: string = "";
    userTermsMessage: string = "";
    buttonText: string = "";
    here_link: string = "";
    showCookiesAcceptance(): void {
        Swal.fire({
            html: HTML_EMOJI.COOKIES + ' ' + this.cookiesMessage,
            showDenyButton: false,
            showCancelButton: false,
            confirmButtonText: `${this.buttonText}`,
            backdrop: false,
            confirmButtonColor: '#F59E0B',

        }).then((result) => {
            if (result.isConfirmed) {
                this.cookiesService.AcceptCondition(true);
            }
        });
    }
    onClickTerms() {
        alert("clicked");
    }
}
