import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LanguageEnum } from 'src/app/core/enumaration/language-enum';
import { AuthService } from 'src/app/core/service/authentication/auth.service';
import { LanguageService } from 'src/app/core/service/language.service';
import { LANGUAGE_EN } from '../languages/en';
import { LANGUAGE_FR } from '../languages/fr';
import { LANGUAGE_MG } from '../languages/mg';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit {
  language: any = {};
  constructor(
    public authService: AuthService,
    private languageService: LanguageService,
    private router: Router
  ) { 
    this.checkLanguage();
  }
  checkLanguage() {
    if(this.languageService.GetLanguage() == LanguageEnum.FR){
      this.language = LANGUAGE_FR;
    }else
    if(this.languageService.GetLanguage() == LanguageEnum.EN){
      this.language = LANGUAGE_EN;
    }
    
  }
  ngOnInit(): void {
  }
  onNavigate(link){
    this.router.navigateByUrl(link);
  }

}
