import { Component, OnInit } from '@angular/core';
import { Payment } from 'src/app/core/entity/payment';
import { NewRencontreReq } from 'src/app/core/entity/rencontre/new-rencontre-req';
import { LoadingService } from 'src/app/core/service/loading.service';
import { PaymentService } from 'src/app/core/service/payment.service';

@Component({
  selector: 'app-rencontre-create',
  templateUrl: './rencontre-create.component.html',
  styleUrls: ['./rencontre-create.component.scss']
})
export class RencontreCreateComponent implements OnInit {

  constructor(
    private loadingService: LoadingService,
    private paymentService: PaymentService
  ) { }
  newRencontreReq: NewRencontreReq = new NewRencontreReq();
  ngOnInit(): void {

    this.loadingService.emitChange(false);
  }
  save() {
    let payment = new Payment();
    payment.entityName = "rencontre";
    payment.status = true;
    payment.entity = this.newRencontreReq;
    this.paymentService.emitChange(payment);
    this.loadingService.emitChange(false);
  }

}
