import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ActionConfig } from 'src/app/core/entity/action-config';
import { Job } from 'src/app/core/entity/job/job';
import { Location } from '@angular/common';
import { LoadingService } from 'src/app/core/service/loading.service';

@Component({
  selector: 'app-job-detail',
  templateUrl: './job-detail.component.html',
  styleUrls: ['./job-detail.component.scss']
})
export class JobDetailComponent implements OnInit {
  actionConfigs: Array<ActionConfig> = new Array<ActionConfig>();
  private state$: Observable<Job>;
  constructor(
    private activatedRouter: ActivatedRoute,
    private location: Location,
    private loadingService: LoadingService
  ) { }
  job: Job = new Job();
  ngOnInit(): void {

    this.state$ = this.activatedRouter.paramMap.pipe(map(() => window.history.state));
    this.state$.subscribe(job => {
      if (job.job_id != null) {
        this.job = job;
        console.log("JOB", this.job);
      } else {
        this.location.back();
      }
    });
    this.loadingService.emitChange(false);
  }

}
