
import { Observable, of, throwError } from 'rxjs';
import { Injectable } from '@angular/core';
import { HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { map, catchError } from 'rxjs/operators';
import { AuthService } from 'src/app/core/service/authentication/auth.service';
import { UserLoginReq } from 'src/app/core/entity/user/user-login-req';
import { ToastrService } from 'ngx-toastr';
import { ApiService } from 'src/app/core/service/api.service';
import { environment } from 'src/environments/environment';
import { User } from 'src/app/core/entity/user/user';
import { JwtHelperService } from '@auth0/angular-jwt';
import { Router } from '@angular/router';
import { JobService } from 'src/app/core/service/job/job.service';
import { Job } from 'src/app/core/entity/job/job';
import { PickListService } from 'src/app/core/service/pick-list.service';
import { PickList } from 'src/app/core/entity/pickList/pickList';
import { SweetAlertService } from 'src/app/core/service/sweet-alert.service';
import { VanillaPayService } from 'src/app/core/service/vanillapay.service';
import { VanillaPay } from 'src/app/core/entity/vanilla-pay';


@Injectable()
export class ApiVanillaPayService extends VanillaPayService {
    getPaymentLink(showErrorNotif: boolean, vanillaPay: VanillaPay): Observable<HttpResponse<string> | Observable<never>> {
        return this.apiService.post<VanillaPay>(environment.api_process_payments, vanillaPay).pipe(
            map((x: HttpResponse<VanillaPay>) => {
                return this.handleResponse<string>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    // getPickListByGroup(showErrorNotif: boolean, group: string): Observable<Observable<never> | HttpResponse<PickList[]>> {
    //     return this.apiService.get<Array<PickList>>(environment.api_get_category_by_groupe + group).pipe( //
    //         map((x: HttpResponse<Array<PickList>>) => {
    //             return this.handleResponse<Array<PickList>>(showErrorNotif, x);
    //         }),
    //         catchError(error => {
    //             return this.catchError(showErrorNotif, error);
    //         })
    //     );
    // }

    constructor(
        private apiService: ApiService,
        private toastrService: ToastrService,
        private router: Router,
        private sweetAlertService: SweetAlertService
    ) {
        super();
    }
    catchError(showErrorNotif: boolean, error: any): Observable<never> {
        if (error instanceof Error) {
            throw new Error(error.message);
        } else {
            this.sweetAlertService.showToastr('Une erreur s\'est produite');
            throw '';
        }
    }
    handleResponse<T>(showErrorNotif: boolean, response: any): HttpResponse<T> {
        if (showErrorNotif && response.status == 202) {
            throw new Error(response.body.toString());
        }
        return response;
    }
}