import { HttpResponse } from '@angular/common/http'
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { PickList } from '../entity/pickList/pickList';

@Injectable()
export abstract class PickListService {
    abstract getAllPickList(showErrorNotif: boolean) : Observable<HttpResponse<Array<PickList>> | Observable<never>>;
    abstract getPickListByGroup(showErrorNotif: boolean, group: string) : Observable<HttpResponse<Array<PickList>> | Observable<never>>;
}