import { Injectable } from "@angular/core";
import { CookiesService } from "src/app/core/service/cookies.service";
@Injectable()
export class ApiCookiesService extends CookiesService {
    AcceptCondition(status: boolean): void {
        const name = "cookies_acceptance";
        const days = 360;
        var expires = "";
        if (days) {
            var date = new Date();
            date.setTime(date.getTime() + (days*24*60*60*1000));
            expires = "; expires=" + date.toUTCString();
        }
        document.cookie = name + "=" + (status || "")  + expires + "; path=/";
    }
    CheckCondition(): boolean {
        const name = "cookies_acceptance";
        var nameEQ = name + "=";
        var ca = document.cookie.split(';');
        for(var i=0;i < ca.length;i++) {
            var c = ca[i];
            while (c.charAt(0)==' ') c = c.substring(1,c.length);
            if (c.indexOf(nameEQ) == 0) return JSON.parse( c.substring(nameEQ.length,c.length));
        }
        return null;    
    }

    CheckUseTerms(): boolean {
        const name = "user_terms";
        var nameEQ = name + "=";
        var ca = document.cookie.split(';');
        for(var i=0;i < ca.length;i++) {
            var c = ca[i];
            while (c.charAt(0)==' ') c = c.substring(1,c.length);
            if (c.indexOf(nameEQ) == 0) return JSON.parse( c.substring(nameEQ.length,c.length));
        }
        return null;   
    }

    AcceptUserTerms(status: boolean): void {
        const name = "user_terms";
        const days = 360;
        var expires = "";
        if (days) {
            var date = new Date();
            date.setTime(date.getTime() + (days*24*60*60*1000));
            expires = "; expires=" + date.toUTCString();
        }
        document.cookie = name + "=" + (status || "")  + expires + "; path=/";
    }
    
}

