import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ActionConfig } from 'src/app/core/entity/action-config';
import { Rencontre } from 'src/app/core/entity/rencontre/rencontre';
import { LoadingService } from 'src/app/core/service/loading.service';
import { Location } from '@angular/common';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-rencontre-detail',
  templateUrl: './rencontre-detail.component.html',
  styleUrls: ['./rencontre-detail.component.scss']
})
export class RencontreDetailComponent implements OnInit {
  actionConfigs: Array<ActionConfig> = new Array<ActionConfig>();
  private state$: Observable<Rencontre>;
  constructor(
    private activatedRouter: ActivatedRoute,
    private location: Location,
    private loadingService: LoadingService
  ) {
    this.user_image_uri = environment.api_host + environment.user_image_url;
  }
  rencontre: Rencontre = new Rencontre();
  ngOnInit(): void {
    this.state$ = this.activatedRouter.paramMap.pipe(map(() => window.history.state));
    this.state$.subscribe(rencontre => {
      if (rencontre.rencontre_id != null) {
        this.rencontre = rencontre;
      } else {
        this.location.back();
      }
    });
    this.loadingService.emitChange(false);
  }
  user_image_uri: string = "";
}
