import { HttpResponse } from '@angular/common/http'
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { GetByCategoryReq } from '../entity/annonce/get-by-category-req';
import { DataTable } from '../entity/data-table';
import { Vente } from '../entity/vente/vente';

@Injectable()
export abstract class VenteService {
    abstract getVentes(showErrorNotif: boolean): Observable<HttpResponse<Array<Vente>> | Observable<never>>
    abstract getLimitedVente(showErrorNotif: boolean, limit: number): Observable<HttpResponse<Array<Vente>> | Observable<never>>;
    abstract getUserVente(showErrorNotif: boolean, userId: number): Observable<HttpResponse<Array<Vente>> | Observable<never>>
    abstract createVente(showErrorNotif: boolean, vente: Vente): Observable<HttpResponse<string> | Observable<never>>;
    abstract GetByCategory(showErrorNotif: boolean, categoryReq: GetByCategoryReq): Observable<HttpResponse<Vente[]> | Observable<never>>;
    abstract CreateVentes(showErrorNotif: boolean, vente: FormData): Observable<HttpResponse<string> | Observable<never>>;
    abstract GetPaginated(showErrorNotif: boolean, tableParams: FormData): Observable<HttpResponse<DataTable> | Observable<never>>;
}