import { HttpResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { JwtHelperService } from '@auth0/angular-jwt';
import { User } from 'src/app/core/entity/user/user';
import { UserService } from 'src/app/core/service/user/user.service';
import { Location } from '@angular/common';
import { Job } from 'src/app/core/entity/job/job';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';
import { AnnonceService } from 'src/app/core/service/annonce/annonce.service';
import { Annonce } from 'src/app/core/entity/annonce/annonce';
import { JobService } from 'src/app/core/service/job/job.service';
import { LoadingService } from 'src/app/core/service/loading.service';
import { AuthService } from 'src/app/core/service/authentication/auth.service';
import { VenteService } from 'src/app/core/service/vente.service';
import { Vente } from 'src/app/core/entity/vente/vente';
import { ItemsNumber } from 'src/app/core/entity/items-number';
import { ItemsNumberService } from 'src/app/core/service/items-number.service';
import { RencontreService } from 'src/app/core/service/rencontre/rencontre.service';
import { Rencontre } from 'src/app/core/entity/rencontre/rencontre';

@Component({
  selector: 'app-user-information',
  templateUrl: './user-information.component.html',
  styleUrls: ['./user-information.component.scss']
})
export class UserInformationComponent implements OnInit {
  user: User = new User();
  image_uri: string = environment.api_host + environment.image_url;
  user_image_uri: string = environment.api_host + environment.user_image_url;
  itemsNumber: ItemsNumber = new ItemsNumber();
  constructor(
    public jwtHelper: JwtHelperService,
    private userService: UserService,
    public location: Location,
    private router: Router,
    private annonceService: AnnonceService,
    private jobService: JobService,
    private loadingService: LoadingService,
    private authService: AuthService,
    private venteService: VenteService,
    private itemsNumberService: ItemsNumberService,
    private rencontreService: RencontreService
  ) { }
  switchTab: string = "annonce";
  userId: number = 0;

  annonces: Array<Annonce> = [];
  jobs: Array<Job> = [];
  ventes: Array<Vente> = [];
  ngOnInit(): void {
    this.userId = this.authService.GetUserParam('user_id');
    this.userService.GetUser(false, this.userId).subscribe((response: HttpResponse<User>) => {
      this.user = response.body['data'][0];
      this.loadingService.emitChange(false);
    });
    this.getAnnonces();
    this.getJob();
    this.getVentes();
    this.loadingService.emitChange(false);
  }
  onClickJobDetails(job) {
    this.router.navigateByUrl(
      environment.uri_job_details, {
      state: job as Job
    });
  }
  getAnnonces() {
    this.annonceService.GetUserAnnonces(false, this.userId).subscribe((response: HttpResponse<Annonce[]>) => {
      this.annonces = response.body['data'];
      this.switchTab = "annonce";
      this.loadingService.emitChange(false);
    });
  }
  getJob() {
    this.jobService.GetUserJobs(false, this.userId).subscribe((response: HttpResponse<Job[]>) => {
      this.jobs = response.body['data'];
      this.switchTab = "job";
      this.loadingService.emitChange(false);
    });
  }
  getVentes() {
    this.venteService.getUserVente(false, this.userId).subscribe((response: HttpResponse<Vente[]>) => {
      this.ventes = response.body['data'];
      this.switchTab = "vente";
      this.loadingService.emitChange(false);
    });
  }
  rencontres: Array<Rencontre>= [];
  getRencontre() {
    this.rencontreService.GetUserRencontre(false, this.userId).subscribe((response: HttpResponse<Rencontre[]>) => {
      this.rencontres = response.body['data'];
      this.switchTab = "rencontre";
      this.loadingService.emitChange(false);
    });
  }
  onClickSwitch(data) {
    switch (data) {
      case "annonce":
        this.switchTab = "annonce";
        this.getAnnonces();
        break;
      case "job":
        this.switchTab = "job";
        this.getJob();
        break;
      case "rencontre":
        this.switchTab = "rencontre";
        this.getRencontre();
        break;
      case "vente":
        this.switchTab = "vente";
        this.getVentes();
        break;

      default:
        break;
    }
  }
  onClickDetailAnnonce(annonce) {
    this.router.navigateByUrl(
      environment.uri_annonce_detail, {
      state: annonce as Annonce
    });
  }
  onClickDetailVente(vente) {
    this.router.navigateByUrl(
      environment.uri_vente_detail, {
      state: vente as Vente
    }
    )
  }
  onClickRencontreDetails(rencontre) {
    this.router.navigateByUrl(
      environment.uri_rencontre_details, {
      state: rencontre as Rencontre
    });
  }
}
