import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { UserArticleComponent } from './user-article/user-article.component';
import { UserCommandeComponent } from './user-commande/user-commande.component';
import { UserInformationComponent } from './user-information/user-information.component';
import { UserPreferenceComponent } from './user-preference/user-preference.component';

const routes: Routes = [
    {path:'',pathMatch:'full',redirectTo:'user-preference'},
    {path:'user-preference', component: UserPreferenceComponent},
    {path:'user-commande', component: UserCommandeComponent},
    {path:'user-information', component: UserInformationComponent},
    {path:'user-article', component: UserArticleComponent},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class userRoutingModule { }
