import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AchatVenteCreateComponent } from './achat-vente-create/achat-vente-create.component';
import { AchatVenteDetailComponent } from './achat-vente-detail/achat-vente-detail.component';
import { AchatVenteHomeComponent } from './achat-vente-home/achat-vente-home.component';


const routes: Routes = [
    {path:'',pathMatch:'full',redirectTo:'achat-vente-home'},
    {path:'achat-vente-home', component: AchatVenteHomeComponent},
    {path: 'achat-vente-detail', component: AchatVenteDetailComponent},
    {path:'achat-vente-create', component: AchatVenteCreateComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AchatVenteRoutingModule { }