import { HttpResponse } from '@angular/common/http'
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { DataTable } from '../../entity/data-table';
import { Job } from '../../entity/job/job';
import { NewRencontreReq } from '../../entity/rencontre/new-rencontre-req';
import { Rencontre } from '../../entity/rencontre/rencontre';

@Injectable()
export abstract class RencontreService {
    abstract GetUserRencontre(showErrorNotif: boolean, userId: number): Observable<HttpResponse<Rencontre[]> | Observable<never>>;
    abstract GetLimitedRenconre(showErrorNotif: boolean, limit: number): Observable<HttpResponse<Rencontre[]> | Observable<never>>;
    abstract CreateRencontre(showErrorNotif: boolean, rencontre: NewRencontreReq): Observable<HttpResponse<string> | Observable<never>>;
    abstract GetPaginated(showErrorNotif: boolean, tableParams: FormData): Observable<HttpResponse<DataTable> | Observable<never>>;
}
