import { HttpResponse } from '@angular/common/http'
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { DataTable } from '../../entity/data-table';
import { Job } from '../../entity/job/job';
import { NewJobReq } from '../../entity/job/new-job-req';

@Injectable()
export abstract class JobService {
    abstract GetAllJobs(showErrorNotif: boolean): Observable<HttpResponse<Job[]> | Observable<never>>;
    abstract GetUserJobs(showErrorNotif: boolean, userId: number): Observable<HttpResponse<Job[]> | Observable<never>>;
    abstract GetLimitedJob(showErrorNotif: boolean, limit: number): Observable<HttpResponse<Job[]> | Observable<never>>;
    abstract CreateJob(showErrorNotif: boolean, job: NewJobReq): Observable<HttpResponse<string> | Observable<never>>;
    abstract GetPaginated(showErrorNotif: boolean, tableParams: FormData): Observable<HttpResponse<DataTable> | Observable<never>>;
}
