import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { LanguageEnum } from 'src/app/core/enumaration/language-enum';
import { AuthService } from 'src/app/core/service/authentication/auth.service';
import { LanguageService } from 'src/app/core/service/language.service';
import { environment } from 'src/environments/environment';
import { Icons } from '../Icons/Icons';
import { NAVIGATION_MENU } from '../items/menus/navigation-menu';
import { LANGUAGE_EN } from '../languages/en';
import { LANGUAGE_FR } from '../languages/fr';

@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.scss']
})
export class NavigationComponent implements OnInit {
  language: any = {};
  language_list: Array<any> = [];
  constructor(
    public customIcon: Icons,
    public authService: AuthService,
    public router: Router,
    private languageService: LanguageService
  ) { 
    this.menus = NAVIGATION_MENU;
    this.language_list = Object.values(LanguageEnum);
    this.checkLanguage();
  }
  checkLanguage() {
    if(this.languageService.GetLanguage() == LanguageEnum.FR){
      this.language = LANGUAGE_FR;
    }else
    if(this.languageService.GetLanguage() == LanguageEnum.EN){
      this.language = LANGUAGE_EN;
    } 
  }
  menus: Array<any>=[];
  ngOnInit(): void {
    
  }

  onClickNavigation(link){
    this.router.navigateByUrl(link);
  }
  clickLanguageMenu(){
    this.isLanguageMenu = !this.isLanguageMenu;
  }
  isLanguageMenu: boolean = false;
  changeLanguage(lng){
    switch (lng) {
      case LanguageEnum.FR:
          this.languageService.SetLanguage(LanguageEnum.FR);
        break;
        case LanguageEnum.EN:
          this.languageService.SetLanguage(LanguageEnum.EN);
        break;
    }
    this.checkLanguage();
    this.isLanguageMenu = !this.isLanguageMenu;
    window.location.href =  this.router.url; 
  }

  
}
