import { HttpResponse } from '@angular/common/http'
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Annonce } from '../../entity/annonce/annonce';
import { GetByCategoryReq } from '../../entity/annonce/get-by-category-req';
import { DataTable } from '../../entity/data-table';
import { FollowAnnonce } from '../../entity/follow-annonce';
import { Job } from '../../entity/job/job';

@Injectable()
export abstract class AnnonceService {
    abstract GetByLimit(showErrorNotif: boolean, limit: number): Observable<HttpResponse<Annonce[]> | Observable<never>>;
    abstract GetUserAnnonces(showErrorNotif: boolean, userId: number): Observable<HttpResponse<Annonce[]> | Observable<never>>;
    abstract GetAnnonces(showErrorNotif: boolean): Observable<HttpResponse<Annonce[]> | Observable<never>>;
    abstract CreateAnnonces(showErrorNotif: boolean, annonce: FormData): Observable<HttpResponse<string> | Observable<never>>;
    abstract GetByCategory(showErrorNotif: boolean, categoryReq: GetByCategoryReq): Observable<HttpResponse<Annonce[]> | Observable<never>>;
    abstract GetPaginated(showErrorNotif: boolean, tableParams: FormData): Observable<HttpResponse<DataTable> | Observable<never>>;
    abstract Follow(showErrorNotif: boolean, followReq: FollowAnnonce): Observable<HttpResponse<string> | Observable<never>>;
    abstract UnFollow(showErrorNotif: boolean, followReq: FollowAnnonce): Observable<HttpResponse<string> | Observable<never>>;
}