import { HttpResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NewAnnonceReq } from 'src/app/core/entity/annonce/new-annonce-req';
import { Job } from 'src/app/core/entity/job/job';
import { NewJobReq } from 'src/app/core/entity/job/new-job-req';
import { Payment } from 'src/app/core/entity/payment';
import { NewRencontreReq } from 'src/app/core/entity/rencontre/new-rencontre-req';
import { Rencontre } from 'src/app/core/entity/rencontre/rencontre';
import { AnnonceService } from 'src/app/core/service/annonce/annonce.service';
import { AuthService } from 'src/app/core/service/authentication/auth.service';
import { JobService } from 'src/app/core/service/job/job.service';
import { PaymentService } from 'src/app/core/service/payment.service';
import { RencontreService } from 'src/app/core/service/rencontre/rencontre.service';
import { SweetAlertService } from 'src/app/core/service/sweet-alert.service';
import { VenteService } from 'src/app/core/service/vente.service';
import { environment } from 'src/environments/environment';
import Swal from 'sweetalert2';
import { DUREE } from '../items/duree/duree';
import { OPERATORS } from '../items/operators/operators';

@Component({
  selector: 'app-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.scss']
})
export class PaymentComponent implements OnInit {
  isPayment: boolean = false;
  entity: any;
  entityName: string = "";

  constructor(
    private paymentService: PaymentService,
    private cdr: ChangeDetectorRef,
    private jobService: JobService,
    private authServie: AuthService,
    private sweetAlertService: SweetAlertService,
    private router: Router,
    private annonceService: AnnonceService,
    private venteService: VenteService,
    private rencontreService: RencontreService
  ) {
    this.paymentService.changeEmitted$.subscribe(payment => {
     // console.log(payment);
      this.isPayment = payment.status;
      this.entity = payment.entity;
      this.entityName = payment.entityName;
      this.cdr.detectChanges();
    });
    this.operators = OPERATORS;
    this.durees = DUREE;
  }
  operators: Array<any>;
  durees: Array<any>;
  ngOnInit(): void {
    this.montant = this.baseAmount;
  }
  onCancelPayment() {
    this.isPayment = !this.isPayment;
    this.choosedOperator = "Opérator";
    this.operatorBackground = "gray";
    this.finalOperator = "";
    this.choosedDuree = "Durée";
    this.finalDuree = "";
  }
  choosedOperator: string = "Opérateur";
  operatorBackground: string = "gray";
  finalOperator: string = "";
  onChooseOperator(operator) {
    this.choosedOperator = operator.name;
    this.operatorBackground = operator.color;
    this.finalOperator = operator.name;
  }
  choosedDuree: string = "Durée";
  finalDuree: string = "";
  onChooseDuree(duree) {
    this.choosedDuree = duree.name;
    this.finalDuree = duree.name;
  }


  onClickPayment() {
    switch (this.entityName) {
      case "job":
        let job = this.entity as NewJobReq;
        this.createJob(job);
        break;
      case "annonce":
        let annonce = this.entity as FormData;
        this.createAnnonce(annonce);
        break;
      case "vente":
        let vente = this.entity as FormData;
        this.createVente(vente);
        break;
      case "rencontre":
        let rencontre = this.entity as NewRencontreReq;
        this.createRencontre(rencontre);
        break;
    }
  }
  createRencontre(rencontre: NewRencontreReq) {
    rencontre.montant = this.montant;
    rencontre.user_id = this.authServie.GetUserParam('user_id');
    //rencontre.state_id = environment.state_id;
    //rencontre.category_id = environment.category_id;
    this.rencontreService.CreateRencontre(true, rencontre).subscribe((response: HttpResponse<string>) => {
      // this.sweetAlertService.showToastr(response.body['data']);
      // window.location.href = this.router.url;
      if (response.body['status'] == true) {
       location.replace(response.body['data']);
      }
    });
  }
  createAnnonce(annonce: any) {
    annonce.append('montant', this.montant);
    this.annonceService.CreateAnnonces(true, annonce).subscribe((response: HttpResponse<string>) => {
      if (response.status == 200) {
        if (response.body['status'] == true) {
          location.replace(response.body['data']);
        }
      }
    });
  }
  createJob(job: NewJobReq) {
    job.montant = this.montant;
    job.user_id = this.authServie.GetUserParam('user_id');
   // job.state_id = environment.state_id;
    //job.category_id = environment.category_id;
    this.jobService.CreateJob(true, job).subscribe((response: HttpResponse<string>) => {
      if (response.body['status'] == true) {
        location.replace(response.body['data']);
      }
    });
  }

  createVente(vente: FormData) {
    vente.append('montant', this.montant);
    this.venteService.CreateVentes(true, vente).subscribe((response: HttpResponse<string>) => {
      if (response.status == 200) {
        if (response.body['status'] == true) {
          location.replace(response.body['data']);
        }
      }
    });
  }
  montant: any = 500;
  baseAmount: number = 500;

  selectedDuree: string = "une semaine";
  onChangeDuree() {

    switch (this.selectedDuree) {
      case this.durees[0].name:
        this.montant = this.baseAmount;
        break;
      case this.durees[1].name:
        this.montant = this.baseAmount * 2;
        break;
      case this.durees[2].name:
        this.montant = this.baseAmount * 3;
        break;
      case this.durees[3].name:
        this.montant = this.baseAmount * 4;
        break;
      case this.durees[4].name:
        this.montant = this.baseAmount * 8;
        break;
      case this.durees[5].name:
        this.montant = this.baseAmount * 12;
        break;
      default:

        break;
    }
  }
}
