import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ActionConfig } from 'src/app/core/entity/action-config';
import { Annonce } from 'src/app/core/entity/annonce/annonce';
import { User } from 'src/app/core/entity/user/user';
import { Vente } from 'src/app/core/entity/vente/vente';
import { Location } from '@angular/common';
import { environment } from 'src/environments/environment';
import { SweetAlertService } from 'src/app/core/service/sweet-alert.service';

@Component({
  selector: 'app-achat-vente-detail',
  templateUrl: './achat-vente-detail.component.html',
  styleUrls: ['./achat-vente-detail.component.scss']
})
export class AchatVenteDetailComponent implements OnInit {
  image_uri: string = environment.api_host + environment.image_url;
  public User: User = new User()
  actionConfigs: Array<ActionConfig> = new Array<ActionConfig>();
  private state$: Observable<Vente>;
  constructor(
    private activatedRouter: ActivatedRoute,
    private location: Location,
    private sweetAlertService: SweetAlertService
  ) { }
  vente: Vente = new Vente();

  ngOnInit(): void {
    this.state$ = this.activatedRouter.paramMap.pipe(map(() => window.history.state));
    this.state$.subscribe(vente => {
      if (vente.vente_id != null) {
        this.vente = vente;
        this.thumbBigImage = this.image_uri + vente.images[0].value;
      } else {
        this.location.back();
      }
    });
  }
  thumbBigImage: string = "";
  onClickThumbNail(data) {
    this.thumbBigImage = this.image_uri + data.filename;
  }
  onClickAcheter(owner) {
    console.log(owner);
    let html1 = "<p class='text-bold text-xl'>" + owner.phone + "</p>";
    let html2 = "<p class='text-bold text-xl'>" + owner.email + "</p>";
    this.sweetAlertService.showModal(html1 + html2);
  }


}
