import { HttpResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { LoginReq } from 'src/app/core/entity/user/login-req';
import { UserLoginReq } from 'src/app/core/entity/user/user-login-req';
import { LanguageEnum } from 'src/app/core/enumaration/language-enum';
import { FormDataPipe } from 'src/app/core/pipes/form-data.pipe';
import { AuthService } from 'src/app/core/service/authentication/auth.service';
import { LanguageService } from 'src/app/core/service/language.service';
import { LoadingService } from 'src/app/core/service/loading.service';
import { environment } from 'src/environments/environment';
import { LANGUAGE_EN } from '../../theme/languages/en';
import { LANGUAGE_FR } from '../../theme/languages/fr';
import { LANGUAGE_MG } from '../../theme/languages/mg';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  message: string;
  title: string;
  placeholder: string;
  result: Subject<string> = new Subject();
  content: string = '';
  f = {}
  userLoginReq: UserLoginReq = new UserLoginReq();
  loginReq: LoginReq = new LoginReq();
  language: any = {};
  constructor(
    private authService: AuthService,
    private toastrService: ToastrService,
    private router: Router,
    private loadingService: LoadingService,
    private languageService: LanguageService
  ) {
    if(this.languageService.GetLanguage() == LanguageEnum.FR){
      this.language = LANGUAGE_FR;
    }
    if(this.languageService.GetLanguage() == LanguageEnum.EN){
      this.language = LANGUAGE_EN;
    }
   }
  formDataPipe = new FormDataPipe();
  ngOnInit(): void {
    this.loadingService.emitChange(false);
    // this.loginReq.password = "1230";
    // this.loginReq.username = "naojulius";
   
  }
  save() {
    // if (this.userLoginReq.Username != null && this.userLoginReq.Password != null) {
    //   this.authService.Login(this.formDataPipe.transform(this.userLoginReq)).subscribe((response: HttpResponse<string>) => {
    //     localStorage.setItem('token', response.body['token']);
    //     this.router.navigateByUrl(environment.uri_home);
    //   }, error => {
    //     this.loadingService.emitChange(false);
    //   });
    // }

    if (this.loginReq.username != null && this.loginReq.password != null) {
      this.authService.Login(this.loginReq).subscribe((response: HttpResponse<string>) => {
       localStorage.setItem('token', response.body['data']);
        this.router.navigateByUrl(environment.uri_home);
      }, error => {
        this.loadingService.emitChange(false);
      });
    }
  }
}
