
import { Observable, of, throwError } from 'rxjs';
import { Injectable } from '@angular/core';
import { HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { map, catchError } from 'rxjs/operators';
import { AuthService } from 'src/app/core/service/authentication/auth.service';
import { UserLoginReq } from 'src/app/core/entity/user/user-login-req';
import { ToastrService } from 'ngx-toastr';
import { ApiService } from 'src/app/core/service/api.service';
import { environment } from 'src/environments/environment';
import { User } from 'src/app/core/entity/user/user';
import { JwtHelperService } from '@auth0/angular-jwt';
import { Router } from '@angular/router';
import { NotificationService } from 'src/app/core/service/notification.service';
import { SweetAlertService } from 'src/app/core/service/sweet-alert.service';
import { LoginReq } from 'src/app/core/entity/user/login-req';


@Injectable()
export class ApiAuthService extends AuthService {
    IsAuthenticated(): boolean {
        const token = localStorage.getItem('token');
        return !this.jwtHelper.isTokenExpired(token);
    }
    LogOut() {
        localStorage.removeItem('token');
        window.location.href = this.router.url;
        this.sweetAlertService.showToastr('Déconnexion réussie, à bientôt.');
    }
    GetUserParam(param: any): any {
        const token = localStorage.getItem('token');
        let decodedToken = this.jwtHelper.decodeToken(token);
        if (param != 'usertoken') {
            return decodedToken ? decodedToken[param] : '';
        } else {
            return token;
        }
    }
    Registration(formData: FormData): Observable<Observable<never> | HttpResponse<string>> {
        let showErrorNotif: boolean = false;
        return this.apiService.PostMultipart<string>(environment.api_auth_registration, formData).pipe(
            map((x: HttpResponse<string>) => {
                return this.handleResponse<string>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    Login(login: LoginReq): Observable<HttpResponse<string> | Observable<never>> {
        let showErrorNotif: boolean = false;
        return this.apiService.post<string>(environment.api_auth_login, login).pipe(
            map((x: HttpResponse<string>) => {
                return this.handleResponse<string>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    constructor(
        private apiService: ApiService,
        private toastrService: ToastrService,
        public jwtHelper: JwtHelperService,
        private router: Router,
        private notify: NotificationService,
        private sweetAlertService: SweetAlertService
    ) {
        super();
    }
    catchError(showErrorNotif: boolean, error: any): Observable<never> {
        if (error instanceof Error) {
            throw new Error(error.message);
        } else {
            this.sweetAlertService.showToastr(error);
            throw '';
        }
    }
    handleResponse<T>(showErrorNotif: boolean, response: any): HttpResponse<T> {
        if (showErrorNotif && response.status == 202) {
            throw new Error(response.body.toString());
        }
        if (response.status == 200) {
            this.sweetAlertService.showToastr('Connexion avec succès.');
            //this.notify.OK("Authentication", "Connexion réussie!");
        }
        return response;
    }
}