
import { Observable, of, throwError } from 'rxjs';
import { Injectable } from '@angular/core';
import { HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { ToastrService } from 'ngx-toastr';
import { ApiService } from 'src/app/core/service/api.service';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { catchError, map } from 'rxjs/operators';
import { AnnonceService } from 'src/app/core/service/annonce/annonce.service';
import { Annonce } from 'src/app/core/entity/annonce/annonce';
import { GetByCategoryReq } from 'src/app/core/entity/annonce/get-by-category-req';
import { SweetAlertService } from 'src/app/core/service/sweet-alert.service';
import { DataTable } from 'src/app/core/entity/data-table';
import { FollowAnnonce } from 'src/app/core/entity/follow-annonce';


@Injectable()
export class ApiAnnonceService extends AnnonceService {
    UnFollow(showErrorNotif: boolean, followReq: FollowAnnonce): Observable<Observable<never> | HttpResponse<string>> {
        return this.apiService.post<string>(environment.api_unfollow_annonce, followReq).pipe(
            map((x: HttpResponse<string>) => {
                return this.handleResponse<string>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    Follow(showErrorNotif: boolean, followReq: FollowAnnonce): Observable<Observable<never> | HttpResponse<string>> {
        return this.apiService.post<string>(environment.api_follow_annonce, followReq).pipe(
            map((x: HttpResponse<string>) => {
                return this.handleResponse<string>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    GetPaginated(showErrorNotif: boolean, tableParams: FormData): Observable<Observable<never> | HttpResponse<DataTable>> {
        return this.apiService.PostMultipart<DataTable>(environment.api_all_annonce, tableParams).pipe(
            map((x: HttpResponse<DataTable>) => {
                return this.handleResponse<DataTable>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    GetByCategory(showErrorNotif: boolean, categoryReq: GetByCategoryReq): Observable<HttpResponse<Annonce[]> | Observable<never>> {
        return this.apiService.post<GetByCategoryReq>(environment.api_annonces_by_category, categoryReq).pipe(
            map((x: HttpResponse<GetByCategoryReq>) => {
                return this.handleResponse<Annonce[]>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    CreateAnnonces(showErrorNotif: boolean, annonce: FormData): Observable<Observable<never> | HttpResponse<string>> {
        return this.apiService.PostMultipart<Annonce>(environment.api_new_annonce, annonce).pipe(
            map((x: HttpResponse<Annonce>) => {
                return this.handleResponse<string>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    GetAnnonces(showErrorNotif: boolean): Observable<Observable<never> | HttpResponse<Annonce[]>> {
        return this.apiService.get<Annonce[]>(environment.api_get_annonces).pipe(
            map((x: HttpResponse<Annonce[]>) => {
                return this.handleResponse<Annonce[]>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    GetUserAnnonces(showErrorNotif: boolean, userId: number): Observable<Observable<never> | HttpResponse<Annonce[]>> {
        return this.apiService.get<Annonce[]>(environment.api_get_user_annonce + userId).pipe(
            map((x: HttpResponse<Annonce[]>) => {
                return this.handleResponse<Annonce[]>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    GetByLimit(showErrorNotif: boolean, limit: number): Observable<HttpResponse<Annonce[]> | Observable<never>> {
        return this.apiService.get<Annonce[]>(environment.api_limited_annonce + limit).pipe(
            map((x: HttpResponse<Annonce[]>) => {
                return this.handleResponse<Annonce[]>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    constructor(
        private apiService: ApiService,
        private toastrService: ToastrService,
        private router: Router,
        private sweetAlertService: SweetAlertService
    ) {
        super();
    }
    catchError(showErrorNotif: boolean, error: any): Observable<never> {
        if (error instanceof Error) {
            throw new Error(error.message);
        } else {
            this.sweetAlertService.showToastr('Une erreur s\'est produite');
            throw '';
        }
    }
    handleResponse<T>(showErrorNotif: boolean, response: any): HttpResponse<T> {
        if (showErrorNotif && response.status == 202) {
            throw new Error(response.body.toString());
        }
        // if (response.status == 200) {
        //     this.toastrService.success('OK', '');
        // }
        return response;
    }
}