import { HttpResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataTable } from 'src/app/core/entity/data-table';
import { PickList } from 'src/app/core/entity/pickList/pickList';
import { Rencontre } from 'src/app/core/entity/rencontre/rencontre';
import { TableParams } from 'src/app/core/entity/table-params';
import { FormDataPipe } from 'src/app/core/pipes/form-data.pipe';
import { LoadingService } from 'src/app/core/service/loading.service';
import { MenuService } from 'src/app/core/service/menu.service';
import { PickListService } from 'src/app/core/service/pick-list.service';
import { RencontreService } from 'src/app/core/service/rencontre/rencontre.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-rencontre-home',
  templateUrl: './rencontre-home.component.html',
  styleUrls: ['./rencontre-home.component.scss']
})
export class RencontreHomeComponent implements OnInit {

  constructor(
    private loadingService: LoadingService,
    private rencontreService: RencontreService,
    private pickListService: PickListService,
    private router: Router,
    private menuService: MenuService
  ) {
    this.tableParams.draw = 20;
    this.tableParams.start = 0;
    this.tableParams.length = 20;

  }
  categories: Array<PickList> = [];
  ngOnInit(): void {
    this.getMenus();
    this.loadingService.emitChange(false);
    this.getPaginatedRencontre();
  }
  menus: Array<any> = [];
  getMenus() {
    this.menuService.getMenu(false, 'RENCONTRES').subscribe((response: HttpResponse<any>) => {
      this.menus = response.body['data'];
    }, error => {
      this.loadingService.emitChange(false);
    })
  }
  is_rencontre: boolean = false;
  onClickCreateRencontreButton() {
    this.router.navigateByUrl(environment.uri_create_rencontre);
  }

  rencontres: Array<Rencontre> = [];
  pageNumber: number = 0;
  itemNumber: number = 20;
  isRencontre: boolean = false;
  increments() {
    this.pageNumber++;
    this.getPaginatedRencontre();
  }
  decrements() {
    this.pageNumber--;
    this.getPaginatedRencontre();
  }
  tableParams: TableParams = new TableParams();
  formDataPipe: FormDataPipe = new FormDataPipe();
  getPaginatedRencontre() {
     this.tableParams.start = this.pageNumber;
     this.tableParams['search[value]'] = '';
    this.rencontreService.GetPaginated(false, this.formDataPipe.transform(this.tableParams)).subscribe((response: HttpResponse<DataTable>) => {
      if (response.body['data']['data'].length > 0) {
        this.isRencontre = true;
        this.rencontres = response.body['data']['data'];
      } else {
        this.isRencontre = false;
      }
      this.loadingService.emitChange(false);
    }, error => {
      this.loadingService.emitChange(false);
    });
  };

  onClickRencontreDetails(rencontre) {
    this.router.navigateByUrl(
      environment.uri_rencontre_details, {
      state: rencontre as Rencontre
    });
  }
  searchValue: string = "";
  onSearch(){
    this.tableParams['search[value]'] = this.searchValue;
    this.getPaginatedRencontre();
  }
  filterByMenu(menu){
    this.tableParams['search[value]'] = menu.value;
    this.getPaginatedRencontre();
  }
  searchInputChange(){
    if(!this.searchValue){
      this.tableParams['search[value]'] ="";
      this.getPaginatedRencontre();

    }
  }
}
