import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RencontreCreateComponent } from './rencontre-create/rencontre-create.component';
import { RencontreDetailComponent } from './rencontre-detail/rencontre-detail.component';
import { RencontreHomeComponent } from './rencontre-home/rencontre-home.component';


const routes: Routes = [
    {path:'',pathMatch:'full',redirectTo:'rencontre-home'},
    {path:'rencontre-home', component: RencontreHomeComponent},
    {path: 'rencontre-create', component: RencontreCreateComponent},
    {path: 'rencontre-detail', component: RencontreDetailComponent}
   
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RencontreRoutingModule { }