
import { Injectable } from "@angular/core";
import { LanguageEnum } from "src/app/core/enumaration/language-enum";
import { LanguageService } from "src/app/core/service/language.service";
@Injectable()
export class ApiLanguageService extends LanguageService {
    SetLanguage(language: LanguageEnum): void {
        const name = "language";
        const days = 30;
        var expires = "";
        if (days) {
            var date = new Date();
            date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
            expires = "; expires=" + date.toUTCString();
        }
        document.cookie = name + "=" + (language || "") + expires + "; path=/";
    }
    GetLanguage(): string {
        const name = "language";
        var nameEQ = name + "=";
        var ca = document.cookie.split(';');
        for (var i = 0; i < ca.length; i++) {
            var c = ca[i];
            while (c.charAt(0) == ' ') c = c.substring(1, c.length);
            if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length);
        }
        return LanguageEnum.FR;
    }
}