import { HttpResponse } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DragScrollComponent } from 'ngx-drag-scroll';
import { Annonce } from 'src/app/core/entity/annonce/annonce';
import { Job } from 'src/app/core/entity/job/job';
import { PickList } from 'src/app/core/entity/pickList/pickList';
import { Vente } from 'src/app/core/entity/vente/vente';
import { AnnonceService } from 'src/app/core/service/annonce/annonce.service';
import { JobService } from 'src/app/core/service/job/job.service';
import { LoadingService } from 'src/app/core/service/loading.service';
import { PickListService } from 'src/app/core/service/pick-list.service';
import { VenteService } from 'src/app/core/service/vente.service';
import { environment } from 'src/environments/environment';
import { LabelType, Options } from '@angular-slider/ngx-slider';
import { GetByCategoryReq } from 'src/app/core/entity/annonce/get-by-category-req';
import { LANGUAGE_FR } from '../../theme/languages/fr';
import { LanguageService } from 'src/app/core/service/language.service';
import { LanguageEnum } from 'src/app/core/enumaration/language-enum';
import { LANGUAGE_EN } from '../../theme/languages/en';
import { LANGUAGE_MG } from '../../theme/languages/mg';
import { Rencontre } from 'src/app/core/entity/rencontre/rencontre';
import { RencontreService } from 'src/app/core/service/rencontre/rencontre.service';

@Component({
  selector: 'app-shop-home',
  templateUrl: './shop-home.component.html',
  styleUrls: ['./shop-home.component.scss']
})
export class ShopHomeComponent implements OnInit {
  categories: Array<PickList> = [];
  language: any = {};

  public venteList: Array<Vente> = [];
  skeletonDatas: Array<number> = [];
  is_rencontre: boolean = false;
  constructor(
    private loadingService: LoadingService,
    private jobService: JobService,
    private annonceService: AnnonceService,
    private router: Router,
    private pickListService: PickListService,
    private venteService: VenteService,
    private languageService: LanguageService,
    private rencontreService: RencontreService
  ) {
    this.skeletonDatas = Array(6).fill(4);
    if (this.languageService.GetLanguage() == LanguageEnum.FR) {
      this.language = LANGUAGE_FR;
    }
    if (this.languageService.GetLanguage() == LanguageEnum.EN) {
      this.language = LANGUAGE_EN;
    }
  }
  image_uri: string = environment.api_host + environment.image_url;
  jobs: Array<Job> = [];
  annonces: Array<Annonce> = [];
  ventes: Array<Vente> = [];
  rencontres: Array<Rencontre> = [];
  is_vente: boolean = false;
  is_annonce: boolean = false;
  is_job: boolean = false;
  limited: number = 6;
  ngOnInit(): void {
    this.getJobs();
    this.getAnnonces();
    this.getVentes();
    this.getrencontres();
    this.getCategories();
    // this.pickListService.getPickListByGroup(false, "CATEGORY").subscribe((response: HttpResponse<PickList[]>) => {
    //   this.categories = response.body['data'];
    //   this.loadingService.emitChange(false);
    // }, error => {
    //   this.loadingService.emitChange(false);
    // });
  }
  getJobs() {
    this.jobService.GetLimitedJob(false, this.limited).subscribe((response: HttpResponse<Job[]>) => {
      this.jobs = response.body['data'];
      this.is_job = true;
      this.loadingService.emitChange(false);
    }, error => {
      this.is_job = false;
      this.loadingService.emitChange(false);
    });
  }
  getAnnonces() {
    this.annonceService.GetByLimit(false, this.limited).subscribe((response: HttpResponse<Annonce[]>) => {
      this.annonces = response.body['data'];
      this.is_annonce = true;
      this.loadingService.emitChange(false);
    }, error => {
      this.is_annonce = false;
      this.loadingService.emitChange(false);
    });
  }
  getVentes() {
    this.venteService.getLimitedVente(false, this.limited).subscribe((response: HttpResponse<Vente[]>) => {
      this.ventes = response.body['data'];
      this.is_vente = true;
      this.loadingService.emitChange(false);
    }, error => {
      this.is_vente = false;
      this.loadingService.emitChange(false);
    });
  }
  getrencontres() {
    this.rencontreService.GetLimitedRenconre(false, this.limited).subscribe((response: HttpResponse<Rencontre[]>) => {
      this.rencontres = response.body['data'];
      this.is_rencontre = true;
      this.loadingService.emitChange(false);
    }, error => {
      this.is_rencontre = false;
      this.loadingService.emitChange(false);
    });
  }
  onClickDetail(annonce) {
    this.router.navigateByUrl(
      environment.uri_annonce_detail, {
      state: annonce as Annonce
    });
  }
  onClickDetailVente(vente) {
    this.router.navigateByUrl(
      environment.uri_vente_detail, {
      state: vente as Vente
    });
  }
  onClickJobDetails(job) {
    this.router.navigateByUrl(
      environment.uri_job_details, {
      state: job as Job
    });
  }
  annonceCategoryReq: GetByCategoryReq = new GetByCategoryReq();
  venteCategoryReq: GetByCategoryReq = new GetByCategoryReq();
  onClickCategory(category) {
    if (category == "TOUT") {
      this.getAnnonces();
      this.getVentes();
    } else {
      this.annonceCategoryReq.Category = category.value;
      this.venteCategoryReq.Category = category.value;
      this.annonceService.GetByCategory(false, this.annonceCategoryReq).subscribe((response: HttpResponse<Annonce[]>) => {
        this.annonces = [];
        this.annonces = response.body['data'];
        this.loadingService.emitChange(false);
      }, error => {
        this.loadingService.emitChange(false);
      });
      this.venteService.GetByCategory(false, this.venteCategoryReq).subscribe((response: HttpResponse<Vente[]>) => {
        this.ventes = [];
        this.ventes = response.body['data'];
        this.loadingService.emitChange(false);
      }, error => {
        this.loadingService.emitChange(false);
      });

    }
  }
  minValue: number = 100;
  maxValue: number = 114500000;
  options: Options = {
    floor: 1000,
    ceil: 114500000,
    translate: (value: number, label: LabelType): string => {
      switch (label) {
        case LabelType.Low:
          return '<b class="text-bold text-yellow-700">Min:</b> Ar ' + value;
        case LabelType.High:
          return '<b class="text-bold text-yellow-700">Max:</b> Ar ' + value;
        default:
          return '$' + value;
      }
    }
  };
  category_arrays: Array<PickList> = [];
  getCategories() {
    this.pickListService.getPickListByGroup(false, "RENCONTRES_CATEGORY").subscribe((response: HttpResponse<PickList[]>) => {
      this.categories = [];
      let body = response.body['data'];
      this.category_arrays = body;
      //alpha.concat(numeric)
      // body.forEach(el => {
      //   this.getSousCategories(el);
      // });
      this.getAnnonceCateg();
      this.loadingService.emitChange(false);
    }, error => {
      this.loadingService.emitChange(false);
    });
  }
  getAnnonceCateg() {
    this.pickListService.getPickListByGroup(false, "ANNONCES_CATEGORY").subscribe((response: HttpResponse<PickList[]>) => {

      let body = response.body['data'];
      //this.category_arrays = body;
      let c = this.category_arrays.concat(body);
      this.category_arrays = c.filter((item, pos) => c.indexOf(item) === pos)
      this.categories = this.category_arrays;
      this.getAchatCateg();
      //
      // body.forEach(el => {
      //   this.getSousCategories(el);
      // });
      //this.getAnnonceCateg();
      this.loadingService.emitChange(false);
    }, error => {
      this.loadingService.emitChange(false);
    });
  }
  getAchatCateg() {
    this.pickListService.getPickListByGroup(false, "ACHATS_CATEGORY").subscribe((response: HttpResponse<PickList[]>) => {

      let body = response.body['data'];
      //this.category_arrays = body;
      let c = this.category_arrays.concat(body);
      this.category_arrays = c.filter((item, pos) => c.indexOf(item) === pos)
      this.categories = this.category_arrays;

      //
      // body.forEach(el => {
      //   this.getSousCategories(el);
      // });
      //this.getAnnonceCateg();
      this.loadingService.emitChange(false);
    }, error => {
      this.loadingService.emitChange(false);
    });
  }
  onClickRencontreDetails(rencontre) {
    this.router.navigateByUrl(
      environment.uri_rencontre_details, {
      state: rencontre as Rencontre
    });
  }
}
