import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuardService } from './guards/auth-guard';
import { AboutUsComponent } from './pages/about-us/about-us.component';
import { UserTermsComponent } from './pages/user-terms/user-terms.component';

const routes: Routes = [
  { path: '', pathMatch: 'full', redirectTo: 'shop' },
  {
    path: 'shop',
    loadChildren: () => import("./pages/shop/shop.module").then(m => m.ShopModule)
  },
  {
    path: 'user',
    canActivate: [AuthGuardService],
    loadChildren: () => import("./pages/user/user.module").then(m => m.UserModule)
  },
  {
    path: 'authentication',
    loadChildren: () => import("./pages/authentication/authentication.module").then(m => m.AuthenticationModule)
  },
  {
    path: 'annonce',
    loadChildren: () => import("./pages/annonce/annonce.module").then(m => m.AnnonceModule)
  },
  {
    path: 'job',
    loadChildren: () => import("./pages/job/job.module").then(m => m.JobModule)
  },
  {
    path: 'achat-vente',
    loadChildren: () => import("./pages/achat-vente/achat-vente.module").then(m => m.AchatVenteModule)
  },
  {
    path: 'rencontre',
    loadChildren: () => import("./pages/rencontre/rencontre.module").then(m => m.RencontreModule)
  },
  {
    path: 'vente',
    loadChildren: () => import("./pages/achat-vente/achat-vente.module").then(m => m.AchatVenteModule)
  },
  {
    path: 'user-terms',
    component: UserTermsComponent
  },
  {
    path: 'about-us',
    component: AboutUsComponent
  },
  { path: '**', redirectTo: 'shop' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {scrollPositionRestoration: 'enabled'})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
