
import { Observable, of, throwError } from 'rxjs';
import { Injectable } from '@angular/core';
import { HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { ToastrService } from 'ngx-toastr';
import { ApiService } from 'src/app/core/service/api.service';
import { Router } from '@angular/router';
import { VenteService } from 'src/app/core/service/vente.service';
import { Vente } from 'src/app/core/entity/vente/vente';
import { environment } from 'src/environments/environment';
import { catchError, map } from 'rxjs/operators';
import { GetByCategoryReq } from 'src/app/core/entity/annonce/get-by-category-req';
import { SweetAlertService } from 'src/app/core/service/sweet-alert.service';
import { DataTable } from 'src/app/core/entity/data-table';


@Injectable()
export class ApiVenteService extends VenteService {
    getUserVente(showErrorNotif: boolean, userId: number): Observable<HttpResponse<Vente[]> | Observable<never>> {
        return this.apiService.get<Array<Vente>>(environment.api_get_user_vente + userId).pipe(
            map((x: HttpResponse<Array<Vente>>) => {
                return this.handleResponse<Array<Vente>>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    GetPaginated(showErrorNotif: boolean, tableParams): Observable<Observable<never> | HttpResponse<DataTable>> {
        return this.apiService.PostMultipart<DataTable>(environment.api_all_vente, tableParams).pipe(
            map((x: HttpResponse<DataTable>) => {
                return this.handleResponse<DataTable>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    CreateVentes(showErrorNotif: boolean, vente: FormData): Observable<Observable<never> | HttpResponse<string>> {
        return this.apiService.PostMultipart<Vente>(environment.api_new_vente, vente).pipe(
            map((x: HttpResponse<Vente>) => {
                return this.handleResponse<string>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    GetByCategory(showErrorNotif: boolean, categoryReq: GetByCategoryReq): Observable<HttpResponse<Vente[]> | Observable<never>> {
        return this.apiService.post<GetByCategoryReq>(environment.api_ventes_by_category, categoryReq).pipe(
            map((x: HttpResponse<GetByCategoryReq>) => {
                return this.handleResponse<Vente[]>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    getVentes(showErrorNotif: boolean): Observable<HttpResponse<Vente[]> | Observable<never>> {
        return this.apiService.get<Vente[]>(environment.api_get_ventes).pipe(
            map((x: HttpResponse<Vente[]>) => {
                return this.handleResponse<Vente[]>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    getLimitedVente(showErrorNotif: boolean, limit: number): Observable<HttpResponse<Vente[]> | Observable<never>> {
        return this.apiService.get<Vente[]>(environment.api_limited_vente + limit).pipe(  //
            map((x: HttpResponse<Vente[]>) => {
                return this.handleResponse<Vente[]>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    createVente(showErrorNotif: boolean, vente: Vente): Observable<Observable<never> | HttpResponse<string>> {
        return this.apiService.post<Vente>(environment.api_create_annonce, vente).pipe(
            map((x: HttpResponse<Vente>) => {
                return this.handleResponse<string>(showErrorNotif, x);
            }),
            catchError(error => {
                return this.catchError(showErrorNotif, error);
            })
        );
    }
    constructor(
        private apiService: ApiService,
        private toastrService: ToastrService,
        private router: Router,
        private sweetAlertService: SweetAlertService
    ) {
        super();
    }
    catchError(showErrorNotif: boolean, error: any): Observable<never> {
        if (error instanceof Error) {
            throw new Error(error.message);
        } else {
            this.sweetAlertService.showToastr('Une erreur s\'est produite');
            throw '';
        }
    }
    handleResponse<T>(showErrorNotif: boolean, response: any): HttpResponse<T> {
        if (showErrorNotif && response.status == 202) {
            throw new Error(response.body.toString());
        }
        // if (response.status == 200) {
        //     this.toastrService.success('OK', '');
        // }
        return response;
    }
}